/* =========================================================================================================================
*	We're done with all the resets & utility classes:
*	ADD GLOBAL SITE STYELS HERE - See your changes right away in elements.html
*
*	be general with these. We want to allow as much recycling as possible
*	========================================================================================================================= */
/* -----  Basic Colors & default sizing use LESS ----- */
/* -----  Basic styling  ----- */
html {
  height: 100%;
  font-size: 62.5%;
}
body {
  color: #333333;
  height: 100%;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 2rem;
  background: linear-gradient(135deg, #044f48, #2a7561);
  background-size: cover;
  background-position: fixed;
  background-repeat: no-repeat;
}
/* base font family, size, color, etc */
a,
a:active,
a:visited {
  color: #FF5E99;
}
a:hover {
  text-decoration: none;
}
/* links that go off site */
/* text elements */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
/* First paragraph in a container */
/* use this if you want a drop cap. Note it applies to ALL paragraph tags */
li {
  margin-left: 15px;
}
/* form elements */
/* Use since input[type=text] etc is buggy in most browsers */
::-webkit-input-placeholder {
  color: #999;
  font: 99% sans-serif;
}
input:-moz-placeholder {
  color: #999;
  font: 99% sans-serif;
}
input:required:invalid,
input:focus:invalid {
  -moz-box-shadow: none;
  /* Removes default delete if using own shadow */
}
/* html5 layout elements */
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.bg:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: url('../images/photo-1451186859696-371d9477be93.jpg') no-repeat 0 0;
  filter: blur(80px);
  transform: scale(1.2);
}
/* =========================================================================================================================
*	DEVICE LAYOUT. THIS IS COMMON
*	========================================================================================================================= */
.device {
  z-index: 10;
  position: relative;
  margin: 0 auto;
  background-color: transparent;
  background-position: top left;
  background-repeat: no-repeat;
  background-size: contain;
}
.device #screen {
  position: absolute;
  z-index: 1;
  background: lime;
  overflow-y: auto;
  overflow-x: hidden;
}
.device.ios {
  width: 450px;
  height: 885px;
}
.device.ios #screen {
  top: 106px;
  right: 39px;
  bottom: 112px;
  left: 37px;
}
.device.ios #statusBar {
  position: absolute;
  z-index: 10;
  top: 106px;
  right: 39px;
  left: 37px;
  height: 30px;
}
.device.ios #statusBar.dark {
  background: rgba(0, 0, 0, 0.2) url(../images/ios-status-bar.png) no-repeat 0 0;
  background-size: contain;
}
.device.ios #statusBar.white {
  background: rgba(0, 0, 0, 0.2) url(../images/ios-status-bar-white.png) no-repeat 0 0;
  background-size: contain;
}
.device.ios.white {
  background-image: url(../images/iphoneShell.png);
}
